.newNotice{
    border-bottom: 1PX solid #f2f2f2;
    padding: 0.875rem 0.9375rem 0.875rem 1.5625rem;
}
.newNotice .top{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    margin-bottom: 1.875rem;
}
.newNotice .top .icon{
    margin-right: 0.625rem;
    width: 1.1875rem;
    height: 1.1875rem;
}
.newNotice .top .icon img{
    width: 100%;
}
.newNotice .top .tit{
    max-width: calc(100% - 1.875rem);
    color: #666;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}
.newNotice .center{
    color: #999;
    font-size: 0.8125rem;
    margin-bottom: 0.625rem;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space:nowrap
}
.newNotice .bottom{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack:justify;
        -ms-flex-pack:justify;
            justify-content:space-between;
}
.newNotice .bottom p{
    color: #999;
    font-size: 0.8125rem;
    /* width: 50% */
}
.bid {
    padding-top: 6.25rem;
}
/*筛选*/
.screen-box{
    position: relative;
    height: 2.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #fff;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-size: 0.875rem;
    padding: 0 1rem;
    padding-right: 1.5rem;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.screen-box .condition{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    max-width: calc((100% - 2.875rem)/4);
}
.screen-box .condition span{
    text-overflow:ellipsis;
    white-space:nowrap;
    overflow: hidden;
    max-width: calc(100% - 0.21875rem);
    display: inline-block;
}
.screen-box .condition i{
    width: 0;
    height: 0;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    margin-top: 0.21875rem;
    border-left: 0.21875rem solid transparent;
    border-right: 0.21875rem solid transparent;
    border-top: 0.21875rem solid #333;
    border-bottom: 0.21875rem solid transparent;
}
.screen-box .condition.active span{
    color: #FE6603;
}
.screen-box .condition.active i{
    border-bottom: 0.21875rem solid #FE6603;
    border-top: 0.21875rem solid transparent;
    margin-bottom: 0.21875rem;
}
